.button {
  font-size: 18px;
  text-align: center;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px #999;
  color: #fff;
  background-color: #5f4d5a;
}

.button:focus{
  outline: none;
}

.button:hover {background-color: #49375e}

.button:active {
  background-color: #49375e;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.button-text{
  font-family: 'YourFontName';
  color: #e7abc6;
}


@font-face {
  font-family: 'YourFontName'; /*a name to be used later*/
  src: url('./../images/TTNormsStdCond-BlackItalic.ttf'); /*URL to font*/
}