.background {
    background-image: url(../images/pattng.png);
    background-size: cover;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
}

.card:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.card
{
    overflow-x: auto;
    overflow-y: auto;
    max-width: 960px;
    background: url(../images/dart.png) no-repeat center;
    background-size: cover;
    min-width: 320px;
    width: 50%;
    height: 420px;
    top: 20%;
    margin: 0 auto;    
    box-shadow: 5px 6px 51px 0px rgba(34, 54, 69, 0.78);
    border-radius: 9pt;
    }

.card:hover{
    box-shadow: 5px 6px 51px 0px rgba(34, 54, 69, 0.78);
}

.container{
    margin-top: 100px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2vh;
}

.top-text{
    font-size: 20px;
    font-family: 'TT Bold';
    color: #292260;
}


@font-face {
    font-family: 'TT Bold'; /*a name to be used later*/
    src: url('./../images/TTNorms-Bold.ttf'); /*URL to font*/
}

